  display: flex;
}

header {
  width: 100%;
  background-color: #333;
  justify-content: center;
  padding: 10px 0;
  display: flex;
}

.search-form {
  display: flex;
}

.search-form input[type="text"] {
  width: 300px;
  border: none;
  border-radius: 4px 0 0 4px;
  padding: 10px;
  font-size: 16px;
}

.search-form button {
  color: #fff;
  cursor: pointer;
  background-color: #5cb85c;
  border: none;
  border-radius: 0 4px 4px 0;
  padding: 10px 15px;
  font-size: 16px;
}

.search-form button:hover {
